import { About } from "../../components/HomePage/About/About";
import { Faq } from "../../components/HomePage/Faq/Faq";
import { Advice } from "../../components/HomePage/Advice/Advice";
import { Form } from "../../components/HomePage/Form/Form";
import { Main } from "../../components/HomePage/Main/Main";
import { Motto } from "../../components/HomePage/Motto/Motto";
import { Numbers } from "../../components/HomePage/Numbers/Numbers";
import { Services } from "../../components/HomePage/Services/Services";
import styles from "./HomePage.module.scss";
import { useRef } from "react";

export const HomePage = () => {
  const servicesRef = useRef();
  const formRef = useRef();

  const scrollToServices = () => {
    servicesRef.current.scrollIntoView({ behavior: "smooth" });
  };

  const scrollToForm = () => {
    formRef.current.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <div className={styles.homePage}>
      <Main scrollToServices={scrollToServices} />
      <Motto />
      <Services servicesRef={servicesRef} />
      <Numbers />
      <Advice scrollToForm={scrollToForm} />
      <About />
      <Faq />
      <Form formRef={formRef} />
    </div>
  );
};
